const burger = new justBurger({
  animationSpeed: 400,
  activeClass: 'custom-class',
  menuId: 'menuList',
  isOpen: (burger) => {
    document.getElementById('overlay').classList.add('open');
    
     
  },
  isClose: (burger) => {
    document.getElementById('overlay').classList.remove('open');
   
  }
});